import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: true
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: false
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

let enterTodo = document.getElementById("inputTodo");
let submitTodo = document.getElementById("submitTODO");
let list = document.getElementById("todoList");
let deleteBtn = document.getElementById("deleteBtn");

//Displays the Todos
function displayTodos() {
  list.innerHTML = "";
  todos.forEach((todo) => {
    //Creates the markup for the Todos
    let markup = `<li class="todoItem" id=${todo.todoID}>${todo.todoText}<span class="editBtn"><i class="fa fa-edit"></i></span>
    <span><i class="fa fa-trash"></i></span>
  </li>`;

    list.insertAdjacentHTML("beforeend", markup);
  });

  //Allows Users to click to remove or add the completed line
  for (let i = 0; i < todos.length; i++) {
    let invTodo = list.children.item(i);

    invTodo.addEventListener("click", () => {
      if (invTodo.classList.contains("done")) {
        invTodo.classList.remove("done");
      }
      //Trying to find the ID that connects with the array so we can update the Array
      else invTodo.classList.add("done");
      console.log(invTodo.id);
      console.log(todos.find((todo) => todo.todoID === invTodo.id));
    });
  }
}

//Submits Todos on Click on + button
submitTodo.addEventListener("click", () => {
  todos.push({
    todoID: todos.length + 1,
    todoText: enterTodo.value,
    todoComplete: false
  });
  displayTodos();
});

//Submits Todos on Click on Enter key
enterTodo.addEventListener("keypress", (e) => {
  if (e.key === "Enter") {
    todos.push({
      todoID: todos.length + 1,
      todoText: enterTodo.value,
      todoComplete: false
    });
    displayTodos();
    console.log(todos);
  }
});

//Allows users to delete completed todos
deleteBtn.addEventListener("click", () => {
  let deletedTodos = document.getElementsByClassName("done");
  while (deletedTodos.length > 0) {
    deletedTodos[0].remove();
  }
});

displayTodos();
